
import './App.css';
import Login from './login.js';
import React from "react";
import Register from "./register"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useRouteMatch,
  useParams
} from "react-router-dom";


function App() {
  return (
    // eslint-disable-next-line react/jsx-no-undef
    <Router>
    <Switch>
      <Route exact path="/register">
        <Register/>
      </Route>
      <Route path="/">
        <Login/>
      </Route>
    </Switch>
    </Router>
    
  );
}

export default App;
