import './left.css';
import logo from './11.png';

function Left() {
    return (
      <div >
          <div className="header">Gamers</div>
        <p className = "par">I always observe the people who pass by when I ride an escalator.
             I'll never see most of them again,
             so I imagine a lot of things about their lives... about the day ahead of them.</p>
          <img alt="" className ="image" src={logo}/>
          <div className="name">Hideo Kojima</div>
      </div>
    );
  }
  
  export default Left;